<script>
import { Bar } from "vue-chartjs";

export default {
  extends: Bar,
  name: "BarChart",
  props: ["data"],
  mounted() {
    // reformat in the way the lib wants
    let chartData = {
      labels: [],
      datasets: [
        {
          label: "Day Patient",
          backgroundColor: "#e87a00",
          data: [],
        },
        {
          label: "In Patient",
          backgroundColor: "#d89c60",
          data: [],
        },
        {
          label: "Out Patient",
          backgroundColor: "#045174",
          data: [],
        },
      ],
    };

    // console.log(this.data);
    var unique = [];
    for (let i = 0; i < this.data.length; i++) {

      if (!unique[this.data[i].StatDate]) {
        chartData.labels.push(this.data[i].CompanyId);
        unique[this.data[i].StatDate] = 1;
      }

      switch (this.data[i].StatType) {
        case "DP":
          chartData.datasets[0].data.push(this.data[i].TotalEpisode);
          break;
        case "IP":
          chartData.labels.push(this.data[i].CompanyId);
          chartData.datasets[1].data.push(this.data[i].TotalEpisode);
          break;
        case "OP":
          chartData.datasets[2].data.push(this.data[i].TotalEpisode);
          break;
      }
    }

    this.renderChart(chartData, {
      plugins: {
        datalabels: {
          anchor: "end",
          align: "top",
          formatter: Math.round(2),
          font: {
            weight: "bold",
          },
        },
      },
      scales: {
        xAxes: [
          {
            stacked: true,
          },
        ],
        yAxes: [
          {
            stacked: true,
          },
        ],
      },
      legend: {
        display: true,
        position: "bottom",
      },
      responsive: true,
      maintainAspectRatio: false,
      layout: {
        padding: {
          left: 0,
          right: 0,
          top: 50,
          bottom: 10,
        },
      },
    });
  },
};
</script>