<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12" md="6" offset-md="1">
        <v-select
          v-model="selectCompDaily"
          :items="companies"
          item-text="Name"
          item-value="Id"
          label="Company"
        ></v-select>
      </v-col>

      <v-col cols="12" md="2">
        <v-menu
          v-model="startMenu"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="100px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="startDate"
              label="Start Date"
              prepend-icon="mdi-event"
              readonly
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker v-model="startDate" @input="startMenu = false" no-title scrollable></v-date-picker>
        </v-menu>
      </v-col>

      <v-col cols="12" md="2">
        <v-menu
          v-model="endMenu"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="100px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="endDate"
              label="End Date"
              prepend-icon="mdi-event"
              readonly
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker v-model="endDate" @input="endMenu = false" no-title scrollable></v-date-picker>
        </v-menu>
      </v-col>

      <v-col cols="12" md="1">
        <v-btn small depressed fab @click="searchDaily" class="success">
          <v-icon>mdi-magnify</v-icon>
        </v-btn>
      </v-col>
    </v-row>

    <v-card flat :loading="loading">
      <div class="container">
        <line-chart v-if="isLoaded" :data="dailyEps" />
      </div>
    </v-card>
  </v-container>
</template>

<script>
import LineChart from "@/components/LineChartEps";
import "chartjs-plugin-datalabels";

var date = new Date(),
  y = date.getFullYear(),
  m = date.getMonth();

function toJSONLocal(date) {
  var local = new Date(date);
  local.setMinutes(date.getMinutes() - date.getTimezoneOffset());
  return local.toJSON().substr(0, 10);
}

export default {
  components: {
    LineChart,
  },
  props: {},
  data() {
    return {
      dailyEps: [],
      companies: [],
      selectCompDaily: "ALL",
      selectCompMonthly: "ALL",
      startDate: toJSONLocal(new Date(y, m, 1)),
      endDate: toJSONLocal(new Date()),
      startMenu: "",
      endMenu: "",
      isLoaded: false,
      loading: false,
    };
  },
  mounted() {
      this.requestDailyApiEp();
      this.requestCompListApi();
  },
  methods: {
    resetState() {
      this.isLoaded = false;   
      this.loading = true; 
    },

    requestDailyApiEp() {
      this.resetState();
      // console.log(`company:${this.selectCompDaily}, startDate:${this.startDate}, endDate:${this.endDate}`);
      this.$http
        .get(
          `http://kpjhqawebslb/hits2api/hitsdashboard/api/episode-daily?comp=${this.selectCompDaily}&startDate=${this.startDate}&endDate=${this.endDate}`
        )
        .then((response) => {
          this.dailyEps = response.data;
          this.isLoaded = true;
          this.loading = false;
        })
        .catch((err) => {
          this.errorMessage = err.response.data.error;
        });
    },

    requestCompListApi() {
      this.$http
        .get("http://kpjhqawebslb/hits2api/hitsdashboard/api/comp-list")
        .then((res) => {
          this.companies = res.data;
          this.companies.unshift({ Name: "ALL", Id: "ALL" });
            // console.log(this.companies);
        })
        .catch((err) => {
          this.errorMessage = err.response.data.error;
        });
    },

    searchDaily() {
      this.requestDailyApiEp();
    },
  }
};
</script>