<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12" md="4" offset-md="1">
        <v-select
          v-model="selectCompany"
          :items="companies"
          item-text="Name"
          item-value="Id"
          label="Company"
          multiple
        >
          <template v-slot:prepend-item>
            <v-list-item ripple @click="toggle">
              <v-list-item-action>
                <v-icon :color="selectCompany.length > 0 ? 'indigo darken-4' : ''">{{ icon }}</v-icon>
              </v-list-item-action>
              <v-list-item-content>
                <v-list-item-title>Select All</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-divider class="mt-2"></v-divider>
          </template>
          <template v-slot:append-item>
            <v-divider class="mb-2"></v-divider>
          </template>
        </v-select>
      </v-col>

      <v-col cols="12" md="2">
        <v-select
          v-model="selectEpType"
          :items="episodeType"
          item-text="Description"
          item-value="EpisodeTypeCode"
          label="Episode Type"
        >
        </v-select>
      </v-col>

      <v-col cols="12" md="2">
        <v-menu
          v-model="startMenu"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="100px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="startDate"
              label="Start Date"
              prepend-icon="mdi-event"
              readonly
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker v-model="startDate" @input="startMenu = false" no-title scrollable></v-date-picker>
        </v-menu>
      </v-col>

      <v-col cols="12" md="2">
        <v-menu
          v-model="endMenu"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="100px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="endDate"
              label="End Date"
              prepend-icon="mdi-event"
              readonly
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker v-model="endDate" @input="endMenu = false" no-title scrollable></v-date-picker>
        </v-menu>
      </v-col>

      <v-col cols="12" md="1">
        <v-btn small depressed fab @click="searchDaily" class="success">
          <v-icon>mdi-magnify</v-icon>
        </v-btn>
      </v-col>
    </v-row>

    <v-card flat :loading="loading">
      <div class="container">
        <line-chart v-if="isLoaded" 
          :chartData="compareEps"
          :chartLabels="compareDate" />
      </div>
    </v-card>
  </v-container>
</template>

<script>
import LineChart from "@/components/LineChartEpsCompare";
import "chartjs-plugin-datalabels";
// import { dateToDay } from "@/utils/dateFormatter";

var date = new Date(),
  y = date.getFullYear(),
  m = date.getMonth();

function toJSONLocal(date) {
  var local = new Date(date);
  local.setMinutes(date.getMinutes() - date.getTimezoneOffset());
  return local.toJSON().substr(0, 10);
}

export default {
  components: {
    LineChart,
  },
  props: {},
  data() {
    return {
      compareEps: [],
      compareDate: [],
      companies: [],
      episodeType: [],
      selectCompany: ['01'],
      selectEpType: 'DP',
      startDate: toJSONLocal(new Date(y, m, 1)),
      endDate: toJSONLocal(new Date()),
      startMenu: "",
      endMenu: "",
      isLoaded: false,
      loading: false,
    };
  },
  mounted() {
    this.requestDailyApiEp();
    this.requestCompListApi();
    this.requestEpisodeTypeApi();
  },
  computed: {
    selectAllComp() {
      return this.selectCompany.length === this.companies.length;
    },
    selectSomeComp() {
      return this.selectCompany.length > 0 && !this.selectAllComp;
    },
    icon() {
      if (this.selectAllComp) return "mdi-close-box";
      if (this.selectSomeComp) return "mdi-minus-box";
      return "mdi-checkbox-blank-outline";
    },
  },
  methods: {
    resetState() {
      this.isLoaded = false;
      this.loading = true;
    },

    toggle() {
      this.$nextTick(() => {
        if (this.selectAllComp) {
          this.selectCompany = [];
        } else {
          this.selectCompany = this.companies.slice();
        }
      });
    },

    requestDailyApiEp() {
      this.resetState();
      // console.log(`company:${this.selectCompany}, type:${this.selectEpType}, startDate:${this.startDate}, endDate:${this.endDate}`);
      this.$http
        .get(
          `http://kpjhqawebslb/hits2api/hitsdashboard/api/episode-compare?comp=${this.selectCompany}&startDate=${this.startDate}&endDate=${this.endDate}&type=${this.selectEpType}`
        )
        .then((response) => {
          let datasets = response.data.datasets;
          console.log(datasets);
          datasets.forEach((e) => {
            var randomColor = Math.floor(Math.random() * 16777215).toString(16);
            e.borderColor = "#" + randomColor;
            e.pointBackgroundColor = "#" + randomColor;
            e.borderWidth = 3;
            e.pointBorderColor = "#" + randomColor;
            e.backgroundColor = "#" + randomColor;
            e.fill = false;
            e.lineTension = 0;
            // e.type = 'line';
          });
          this.compareEps = datasets;

          var labels = response.data.labels;
          let lbls = labels.map((d) => d);
          this.compareDate = lbls;
          
          this.isLoaded = true;
          this.loading = false;
        })
        .catch((err) => {
          this.errorMessage = err.response.data.error;
        });
    },

    requestCompListApi() {
      this.$http
        .get("http://kpjhqawebslb/hits2api/hitsdashboard/api/comp-list")
        .then((res) => {
          this.companies = res.data;
          // console.log(this.companies);
        })
        .catch((err) => {
          this.errorMessage = err.response.data.error;
        });
    },

    requestEpisodeTypeApi() {
      this.$http
        .get("http://kpjhqawebslb/hits2api/hitsdashboard/api/episode-type")
        .then((res) => {
          this.episodeType = res.data;
          // console.log(this.episodeType);
        })
        .catch((err) => {
          this.errorMessage = err.response.data.error;
        });
    },

    searchDaily() {
      this.requestDailyApiEp();
    },
  },
};
</script>