<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12" md="2" offset-md="9">
        <v-menu
          v-model="yearCompMenu"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="100px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="yearlyComp"
              label="Year"
              prepend-icon="mdi-event"
              readonly
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="yearlyComp"
            @input="yearCompMenu = false"
            no-title
            scrollable
            type="year"
          ></v-date-picker>
        </v-menu>
      </v-col>

      <v-col cols="12" md="1">
        <v-btn small depressed fab @click="searchYearlyComp" class="success">
          <v-icon>mdi-magnify</v-icon>
        </v-btn>
      </v-col>
    </v-row>

    <v-card flat :loading="loading">
      <div class="container">
        <bar-chart v-if="loaded" :data="yearlyEpsByComp" />
      </div>
    </v-card>
  </v-container>
</template>

<script>
import BarChart from "@/components/BarChartEps";
import "chartjs-plugin-datalabels";

export default {
  components: {
    BarChart,
  },
  props: {},
  data() {
    return {
      loaded: false,
      yearlyEpsByComp: [],
      yearlyComp: new Date().getFullYear(),
      yearCompMenu: "",
      loading: false,
    };
  },
  mounted() {
    this.requestYearlyApiEpComp();
  },
  methods: {
    resetState() {
      this.loaded = false;
      this.loading = true;
    },

    requestYearlyApiEpComp() {
      this.resetState();
      this.$http
        .get(
          `http://kpjhqawebslb/hits2api/hitsdashboard/api/episode-comp-daily?year=${this.yearlyComp}`
        )
        .then((response) => {
          this.yearlyEpsByComp = response.data;
          // console.log(this.yearlyEpsByComp);
          this.loaded = true;
          this.loading = false;
        })
        .catch((err) => {
          this.errorMessage = err.response.data.error;
        });
    },

    searchYearlyComp() {
      this.requestYearlyApiEpComp();
    },
  },
};
</script>