<script>
  import { Line } from 'vue-chartjs'

  export default {
    extends: Line,
    props: {
      chartData: {
        type: Array || Object,
        required: false
      },
      chartLabels: {
        type: Array,
        required: true
      },
    },
    data () {
      return {
        options: {
          scales: {
            yAxes: [{
              ticks: {
                beginAtZero: true
              },
              gridLines: {
                display: true
              },
              scaleLabel: {
                display: true,
                labelString: "Quantity"
              },
            }],
            xAxes: [ {
              gridLines: {
                display: false
              },
              scaleLabel: {
                display: true,
                labelString: "Date"
              }
            }]
          },
          legend: {
            display: true
          },
          responsive: true,
          maintainAspectRatio: false,
          plugins: {
            datalabels: {
              anchor: 'end',
              align: 'top',
              // formatter: Math.round(),
              font: {
                weight: 'bold'
              }
            }
          }
        }
      }
    },
    mounted () {
      this.renderChart({
        labels: this.chartLabels,
        datasets: this.chartData
      }, this.options)
    }
  }
</script>