<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12" md="6" offset-md="3">
        <v-select
          v-model="selectCompMonthly"
          :items="companies"
          item-text="Name"
          item-value="Id"
          label="Company"
        ></v-select>
      </v-col>

      <v-col cols="12" md="2">
        <v-menu
          v-model="yearMenu"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="100px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="yearMonthly"
              label="Year"
              prepend-icon="mdi-event"
              readonly
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="yearMonthly"
            @input="yearMenu = false"
            no-title
            scrollable
            type="year"
          ></v-date-picker>
        </v-menu>
      </v-col>

      <v-col cols="12" md="1">
        <v-btn small depressed fab @click="searchMonthly" class="success">
          <v-icon>mdi-magnify</v-icon>
        </v-btn>
      </v-col>
    </v-row>

    <v-card flat :loading="loading">
      <div class="container">
        <line-chart v-if="isLoaded" :data="monthlyEps" />
      </div>
    </v-card>
  </v-container>
</template>

<script>
import LineChart from "@/components/LineChartEps";
import "chartjs-plugin-datalabels";

export default {
  components: {
    LineChart,
  },
  props: {},
  data() {
    return {
      isLoaded: false,
      monthlyEps: [],
      companies: [],
      selectCompMonthly: "ALL",
      yearMonthly: new Date().getFullYear(),
      yearMenu: "",
      loading: false,
    };
  },
  mounted() {
    this.requestMonthlyApiEp();
    this.requestCompListApi();
  },
  methods: {
    resetState() {
      this.isLoaded = false;
      this.loading = true;
    },

    requestMonthlyApiEp() {
      this.resetState();
      this.$http
        .get(
          `http://kpjhqawebslb/hits2api/hitsdashboard/api/episode-monthly?comp=${this.selectCompMonthly}&year=${this.yearMonthly}`
        )
        .then((response) => {
          this.monthlyEps = response.data;
          // console.log(this.monthlyEps);
          this.isLoaded = true;
          this.loading = false;
        })
        .catch((err) => {
          this.errorMessage = err.response.data.error;
        });
    },

    requestCompListApi() {
      this.$http
        .get("http://kpjhqawebslb/hits2api/hitsdashboard/api/comp-list")
        .then((response) => {
          this.companies = response.data;
          this.companies.unshift({ Name: "ALL", Id: "ALL" });
          //   console.log(this.companies);
        })
        .catch((err) => {
          this.errorMessage = err.response.data.error;
        });
    },

    searchMonthly() {
      this.requestMonthlyApiEp();
    },

  },
};
</script>