<template>
  <v-container fluid>
    <v-row>
      <v-col v-for="ep in yearlyEp" :key="ep.StatType" cols="12" md="4">
        <v-card color="#385F73" dark>
          <div class="d-flex felx-non-wrap justify-space-between">
            <v-avatar color="red" tile size="100">
              <span class="white--text headline">{{ep.StatType}}</span>
            </v-avatar>
            <div>
              <v-card-title>
                <div class="display-1 orange--text text-right">{{ep.TotalEpisode}}</div>
              </v-card-title>
              <v-card-subtitle>
                <div>{{ep.Description}} ({{ep.StatDate}})</div>
              </v-card-subtitle>
            </div>
          </div>
        </v-card>
      </v-col>

      <v-col cols="12">
        <v-card>
          <!-- <v-toolbar flat color="primary" dark dense>
            <v-toolbar-title>Episode</v-toolbar-title>
          </v-toolbar>-->
          <v-tabs>
            <v-tab>
              <v-icon left>mdi-calendar</v-icon>Daily
            </v-tab>
            <v-tab>
              <v-icon left>mdi-calendar</v-icon>Monthly
            </v-tab>
            <v-tab>
              <v-icon left>mdi-calendar</v-icon>Yearly (Companies)
            </v-tab>
            <v-tab>
              <v-icon left>mdi-calendar</v-icon>Compare
            </v-tab>

            <v-tab-item>
              <episode-daily />
            </v-tab-item>

            <v-tab-item>
              <episode-monthly />
            </v-tab-item>

            <v-tab-item>
              <episode-yearly-comp />
            </v-tab-item>

            <v-tab-item>
              <episode-compare />
            </v-tab-item>
          </v-tabs>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import EpisodeDaily from "./EpisodeDaily";
import EpisodeMonthly from "./EpisodeMonthly";
import EpisodeYearlyComp from "./EpisodeYearlyComp";
import EpisodeCompare from "./EpisodeCompare";
import "chartjs-plugin-datalabels";

export default {
  components: {
    EpisodeDaily,
    EpisodeMonthly,
    EpisodeYearlyComp,
    EpisodeCompare,
  },
  props: {},
  data() {
    return {
      loaded: false,
      yearlyEp: [],
    };
  },
  mounted() {
    this.requestYearlyApiEp();
  },
  methods: {
    resetState() {
      this.loaded = false;
      this.showError = false;
    },

    requestYearlyApiEp() {
      this.resetState();
      this.loading = true;
      this.$http
        .get("http://kpjhqawebslb/hits2api/hitsdashboard/api/episode-yearly")
        .then((response) => {
          this.yearlyEp = response.data;
          this.loaded = true;
          this.loading = false;
        })
        .catch((err) => {
          this.errorMessage = err.response.data.error;
          this.showError = true;
          this.loading = false;
        });
    },
  },
};
</script>