<script>
import { Line } from "vue-chartjs";
import { dateToDay, dateToMonth } from "@/utils/dateFormatter";

export default {
  extends: Line,
  name: "LineChart",

  props: {
    data: {
      type: Array || Object,
      required: false,
    },
  },

  data: () => ({
    options: {
      plugins: {
        datalabels: {
          anchor: "end", //center, start, end
          align: "top", //center, start, end, right, bottom, left, top
          formatter: Math.round(2),
          font: {
            weight: "bold",
          },
        },
      },
      scales: {
        yAxes: [
          {
            ticks: {
              beginAtZero: true,
            },
            gridLines: {
              display: true,
            },
            scaleLabel: {
              display: true,
              labelString: "Quantity",
            },
          },
        ],
        xAxes: [
          {
            gridLines: {
              display: false,
            },
            scaleLabel: {
              display: true,
              labelString: "Date",
            },
          },
        ],
      },
      legend: {
        display: true,
        position: "bottom",
      },
      responsive: true,
      maintainAspectRatio: false,
      lineTension: 0,
      layout: {
        padding: {
          left: 10,
          right: 10,
          top: 50,
          bottom: 10,
        },
      },
    },
  }),

  mounted() {
    // reformat in the way the lib wants
    let chartData = {
      labels: [],
      datasets: [
        {
          label: "Day Patient",
          borderColor: "#e87a00",
          pointBackgroundColor: "#e87a00",
          borderWidth: 1,
          pointBorderColor: "#e87a00",
          backgroundColor: "#e87a00",
          fill: false,
          data: [],
          lineTension: 0,
        },
        {
          label: "In Patient",
          borderColor: "#d89c60",
          pointBackgroundColor: "#d89c60",
          borderWidth: 1,
          pointBorderColor: "#d89c60",
          backgroundColor: "#d89c60",
          fill: false,
          data: [],
          lineTension: 0,
        },
        {
          label: "Out Patient",
          borderColor: "#045174",
          pointBackgroundColor: "#045174",
          borderWidth: 1,
          pointBorderColor: "#045174",
          backgroundColor: "#045174",
          fill: false,
          data: [],
          lineTension: 0,
        },
      ],
    };

    // console.log(this.data);
    var unique = [];
    for (let i = 0; i < this.data.length; i++) {

      if (!unique[this.data[i].StatDate]) {
        if (this.data[i].MonthNumber)
          chartData.labels.push(dateToMonth(this.data[i].StatDate));
        else
          chartData.labels.push(dateToDay(this.data[i].StatDate));
        unique[this.data[i].StatDate] = 1;
      }

      switch (this.data[i].StatType) {
        case "DP":
          chartData.datasets[0].data.push(this.data[i].TotalEpisode);
          break;
        case "IP":
          chartData.datasets[1].data.push(this.data[i].TotalEpisode);
          break;
        case "OP":
          chartData.datasets[2].data.push(this.data[i].TotalEpisode);
          break;
      }
    }

    this.renderChart(chartData, this.options);
  },
};
</script>
